import { defaultTheme } from "../assets/defaultThemeConfig";

export const fetchStyles = async () => {
  try {
    // In the future, replace this with an API call
    let theme = {};
    await fetch(
      "http://localhost:3000/api/v1/project/get-project-theme/ac6023bf-5916-4dad-a4e1-7a544d304b5a"
    ).then((res) =>
      res.json((response) => {
        console.log(response);
        theme = response.data.theme;
      })
    );

    const cssVars = {
      // Theme Colors
      "color-primary": theme.themeColors.primary,
      "color-secondary": theme.themeColors.secondary,

      // Typography - Heading
      "font-heading-type": theme.typography.headingFont.type,
      "font-heading-bold": theme.typography.headingFont.variants.bold,
      "font-heading-size-adjustment":
        theme.typography.headingFont.sizeAdjustment,

      // Typography - Body
      "font-body-type": theme.typography.bodyFont.type,
      "font-body-family": theme.typography.bodyFont.family,
      "font-body-regular": theme.typography.bodyFont.variants.regular,

      // Type Scales
      "heading-font": theme.typography.typeScales.heading.font,
      "heading-color": theme.typography.typeScales.heading.color,
      "subheading-font": theme.typography.typeScales.subHeading.font,
      "subheading-color": theme.typography.typeScales.subHeading.color,
      "body-text-color": theme.typography.typeScales.body.color,
      "caption-text-color": theme.typography.typeScales.caption.color,

      // Header Logos
      "header-company-logo-url": `url("${theme.header.companyLogo.imageUrl}")`,
      "header-company-logo-size": `var(--size-${theme.header.companyLogo.size})`,
      "header-company-logo-align": theme.header.companyLogo.alignment,

      "header-event-logo-url": `url("${theme.header.eventLogo.imageUrl}")`,
      "header-event-logo-size": `var(--size-${theme.header.eventLogo.size})`,
      "header-event-logo-align": theme.header.eventLogo.alignment,

      // Buttons
      "btn-primary-text-color": theme.buttons.primary.textColor,
      "btn-primary-rounded": `var(--radius-${theme.buttons.primary.rounded})`,
      "btn-primary-border-thickness": `var(--border-${theme.buttons.primary.borderThickness})`,

      "btn-secondary-text-color": theme.buttons.secondary.textColor,
      "btn-secondary-rounded": `var(--radius-${theme.buttons.secondary.rounded})`,
      "btn-secondary-border-thickness": `var(--border-${theme.buttons.secondary.borderThickness})`,

      // Surfaces
      "surface-bg-color": theme.surfaces.color,
      "surface-rounded": `var(--radius-${theme.surfaces.rounded})`,
      "surface-border-thickness": `var(--border-${theme.surfaces.borderThickness})`,
      "surface-border-color": theme.surfaces.borderColor,

      // Background
      "background-color": theme.background.color,
      "background-image-portrait": `url("${theme.background.portraitImageUrl}")`,
      "background-image-landscape": `url("${theme.background.landscapeImageUrl}")`,
    };

    // Apply to :root
    const root = document.documentElement;
    Object.entries(cssVars).forEach(([key, value]) => {
      if (value) {
        root.style.setProperty(`--${key}`, value);
      }
    });
  } catch (err) {
    console.error("Failed to apply styles:", err);
  }
};
